/** @jsxImportSource @emotion/react */
import React from 'react'
import MuiDrawer from '@mui/material/Drawer'
import MuiButton from '@mui/material/Button'

import { UserName } from './UserName'
import { useApi } from '../../data/api'
import { useNavigate } from '../AppRouter'

export interface SideNavBarProps {
  className?: string
  isOpen: boolean
  onClose: () => void
}

const CenteredDiv = (props) => (
  <div css={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }} {...props} />
)

export const SideNavBar = ({ className, isOpen, onClose }: SideNavBarProps): JSX.Element => {
  const api = useApi()
  const navigate = useNavigate()

  const links = [
    {
      label: 'Boxes',
      onClick: () => navigate.boxes(),
    },
  ]

  return (
    <MuiDrawer anchor="left" open={isOpen} onClose={() => onClose()} className={className}>
      <CenteredDiv css={{ minWidth: '20em', height: '100%' }}>
        <UserName />
        <MuiButton
          onClick={() => {
            api.signOut()
          }}
        >
          Sign Out
        </MuiButton>
        <div css={{ marginTop: '2em', fontSize: '125%', display: 'flex', flexDirection: 'column', gap: '0.5em' }}>
          {links.map(({ label, onClick }) => (
            <a
              key={label}
              onClick={() => {
                onClick()
                onClose()
              }}
            >
              {label}
            </a>
          ))}
        </div>
        <CenteredDiv
          css={{
            marginTop: 'auto',
            padding: '1em 0',
            gap: '0.25em',
          }}
        >
          <span>v{process.env.REACT_APP_VERSION}</span>
          <a href="https://github.com/namc-utah/namc-bug-db/issues/new" target="_blank" rel="noreferrer">
            Report Issue
          </a>
        </CenteredDiv>
      </CenteredDiv>
    </MuiDrawer>
  )
}
