import React, { useState, useCallback } from 'react'
import produce from 'immer'

type UpdatableStateReturnTuple<T> = [
  state: T,
  setState: React.Dispatch<React.SetStateAction<T>>,
  updateState: (updater) => void
]

export const useUpdatableState = <T>(initialState: T): UpdatableStateReturnTuple<T> => {
  const [state, setState] = useState<T>(initialState)
  const updateState = useCallback((updater) => setState((current) => produce(current, updater)), [setState])
  return [state, setState, updateState]
}
