import gql from 'graphql-tag'
import { BoxInfo as GraphBoxInfo } from '@namcbugdb/common'

import { Api } from '../api'
import { Box } from '../../types'
import { transformBox } from './fetchBoxes'

export const fetchBoxByIdQuery = gql`
  query ($boxId: Int!) {
    boxInfo(boxId: $boxId) {
      boxId
      customerName
      customerId
      boxReceivedDate
      submittedBy
      boxState
      sampleCount
      midges
      measurements
      sortComplete
      idComplete
    }
  }
`

export const fetchBoxById =
  (api: Api) =>
  async (boxId: number): Promise<Box | null> => {
    const data = await api.query<{ boxInfo: GraphBoxInfo }>(fetchBoxByIdQuery, { boxId })

    return transformBox({
      ...data.boxInfo,

      // TODO: the current BoxInfo is not returning these properties:
      midges: false,
      submittedBy: '',
      sortComplete: 0,
      idComplete: 0,
    })
  }
