import { ASTNode } from 'graphql'
import log from 'loglevel'

import { Api } from './Api'

export type MockResponderMap = Map<ASTNode, () => unknown>

export class MockApi implements Api {
  responseDelay = 0
  responses = new Map()

  constructor(responders?: MockResponderMap) {
    if (responders) this.setResponses(responders)
  }

  setResponses = (responders: MockResponderMap): void => {
    responders.forEach((responder, query) => this.responses.set(query, responder))
  }

  wait = async (): Promise<void> => {
    return new Promise((resolve) => {
      window.setTimeout(resolve, this.responseDelay)
    })
  }

  signOut = (): void => {
    log.debug('MockApi signOut')
  }

  query = async <T>(query: ASTNode, variables?: { [key: string]: unknown }): Promise<T> => {
    log.debug('MockApi query', query, variables)
    const responder = this.responses.get(query.loc?.source.body)
    await this.wait()
    if (responder) {
      const response = responder(variables)
      log.debug('MockApi responded to query', response)
      return response
    }
    log.debug('MockApi not responding to query')
    return new Promise(() => {
      /* simulate awaiting API response indefinitely */
    })
  }

  queryAllPages = async <QueryT>(
    query: ASTNode,
    variables: { [key: string]: unknown },
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    { dataProperty }: { dataProperty: string }
  ): Promise<QueryT> => {
    log.debug('MockApi queryAllPages', query.loc?.source.body, variables)
    const responder = this.responses.get(query)
    await this.wait()
    if (responder) {
      const response = responder(variables)
      log.debug('MockApi responded to queryAllPages', response)
      return response
    }
    log.debug('MockApi not responding to queryAllPages')
    return new Promise(() => {
      /* simulate awaiting API response indefinitely */
    })
  }

  mutation = async <T>(query: ASTNode, variables?: { [key: string]: unknown }): Promise<T> => {
    log.debug('MockApi mutation', query.loc?.source.body, variables)
    const responder = this.responses.get(query)
    await this.wait()
    if (responder) {
      const response = responder(variables)
      log.debug('MockApi responded to mutation', response)
      return response
    }
    log.debug('MockApi not responding to mutation')
    return new Promise(() => {
      /* simulate awaiting API response indefinitely */
    })
  }
}
