import gql from 'graphql-tag'

import { Api } from '../api'

interface LabSortSubmission {
  sampleId: number
  sorterId: number
  sortTime: number
  sortDate: string
  sorterCount: number
  elutriation: boolean
  labSplit: number
}

export const updateLabSortMutation = gql`
  mutation (
    $sampleId: ID!
    $sorterId: ID!
    $count: Int!
    $elutriation: Boolean!
    $time: Float!
    $sortTimestamp: String!
    $labSplit: Float!
  ) {
    labSort(
      sampleId: $sampleId
      sorterId: $sorterId
      count: $count
      elutriation: $elutriation
      time: $time
      sortTimestamp: $sortTimestamp
      labSplit: $labSplit
    )
  }
`

export const updateLabSort =
  (api: Api) =>
  async (submission: LabSortSubmission): Promise<boolean> => {
    try {
      // TODO: this is truly awful code; surely we can do better with dates?
      const { sortTime: time, sortDate, sorterCount: count, ...rest } = submission
      const sortDateObject = new Date(sortDate)
      const now = new Date()
      const todayDateObject = new Date(
        new Date(now.getTime() - now.getTimezoneOffset() * 60 * 1000).toISOString().substring(0, 10)
      )
      const sortTimestamp = (new Date().getTime() - (todayDateObject.getTime() - sortDateObject.getTime())).toString()

      const variables = {
        time,
        sortTimestamp,
        count,
        ...rest,
      }
      const data = await api.mutation<{
        labSort: number // NOTE: count of records changed
      }>(updateLabSortMutation, variables)

      return !!data.labSort
    } catch (error) {
      return false
    }
  }
