import { atomFamily, selector, selectorFamily } from 'recoil'

import { Box, BoxState, LifeStage, MyUser, NoteType, Sample, TaxaLevel, Taxon, User } from '../../types'
import { apiState } from '../api'
import { withDescendantIds } from '../../lib/taxonomy'
import {
  fetchBoxById,
  fetchBoxes,
  fetchLookups,
  fetchMyUser,
  fetchSampleById,
  fetchSorterUsers,
  fetchTaxa,
} from '../calls'

export const selectTaxa = selector<Taxon[]>({
  key: 'taxa',
  get: async ({ get }) => {
    const api = get(apiState)

    const [taxaLevels, taxa] = await Promise.all([get(selectLookups).taxaLevels, fetchTaxa(api)()])

    return withDescendantIds(
      taxa.map((taxon) => ({
        ...taxon,
        levelRankOrder: taxaLevels.find(({ id }) => id === taxon.levelId)?.rankOrder ?? -1,
      }))
    )
  },
})

const selectLookups = selector<{
  taxaLevels: TaxaLevel[]
  lifeStages: LifeStage[]
  noteTypes: NoteType[]
  boxStates: BoxState[]
}>({
  key: 'lookups',
  get: async ({ get }) => {
    const api = get(apiState)
    return fetchLookups(api)()
  },
})

export const selectLifeStages = selector<LifeStage[]>({
  key: 'lifeStages',
  get: async ({ get }) => get(selectLookups).lifeStages,
})

export const selectNoteTypes = selector<NoteType[]>({
  key: 'noteTypes',
  get: async ({ get }) => get(selectLookups).noteTypes,
})

export const selectBoxStates = selector<BoxState[]>({
  key: 'boxStates',
  get: async ({ get }) => get(selectLookups).boxStates,
})

const sampleByIdState = atomFamily<Sample | null, { id: number; nonce?: string }>({
  key: 'sampleByIdState',
  default: null,
})

export const selectSample = selectorFamily<Sample | null, { id: number; nonce?: string }>({
  key: 'sampleById',
  get:
    ({ id, nonce }) =>
    async ({ get }) => {
      const stateSample = get(sampleByIdState({ id, nonce }))
      if (stateSample) return stateSample
      const api = get(apiState)
      return fetchSampleById(api)(id)
    },
  set:
    ({ id, nonce }) =>
    ({ set }, sample) => {
      set(sampleByIdState({ id, nonce }), sample)
    },
})

export const selectSorterUsers = selector<User[]>({
  key: 'sorterUsers',
  get: async ({ get }) => {
    const api = get(apiState)
    return fetchSorterUsers(api)()
  },
})

export const selectBoxById = selectorFamily<Box | null, number>({
  key: 'boxById',
  get:
    (id) =>
    async ({ get }) => {
      const api = get(apiState)
      return fetchBoxById(api)(id) || null
    },
})

export const selectMyUser = selector<MyUser>({
  key: 'myUser',
  get: async ({ get }) => {
    const api = get(apiState)
    return fetchMyUser(api)()
  },
})

export const selectBoxes = selector<Box[]>({
  key: 'boxes',
  get: async ({ get }) => {
    const api = get(apiState)
    return fetchBoxes(api)()
  },
})
