/* eslint-disable @typescript-eslint/no-non-null-assertion */
/** @jsxImportSource @emotion/react */
import React, { useEffect } from 'react'
import { NAMCResource } from '@namcbugdb/common'
import { round } from 'lodash'

import { useApi } from '../../data/api'
import { useNotifications } from '../AppNotifications'
import { User, Sample, MyUser } from '../../types'
import { SampleInfoPanel } from '../SampleInfoPanel'
import { SelectField, SelectFieldProps } from '../SelectField'
import { NumberField } from '../NumberField'
import { CheckboxField } from '../CheckboxField'
import { DateField } from '../DateField'
import { ConfirmNavigateAway } from '../AppRouter'
import { ToggleEditModeButton } from './ToggleEditModeButton'
import { useUpdatableState } from '../../lib/useUpdatableState'
import { updateLabSort } from '../../data/calls'

const LAB_SPLIT_PERCENT_PRECISION = 3

const getToday = (): string => {
  const now = new Date()
  return new Date(now.getTime() - now.getTimezoneOffset() * 60 * 1000).toISOString().substring(0, 10)
}

export interface SampleSortingProps {
  sorterUsers: User[]
  sample: Sample
  setSample: (sample: Sample) => void
  user: MyUser
}

interface Fields {
  sorterId: number | ''
  labSplitPercent: number | null
  sorterCount: number | null
  sortTime: number | null
  sortDate: string | null
  elutriation: boolean
}

type ControllerState = {
  sample?: Sample
  values: Fields
  errors: Partial<Record<keyof Fields, boolean>>
  editMode: 'stopped' | 'started' | 'stopping'
}

const initialState = {
  values: { sorterId: '', labSplitPercent: null, sorterCount: null, sortTime: null, sortDate: '', elutriation: false },
  editMode: 'stopped',
} as ControllerState

export interface ControllerActions {
  reset: () => void
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  setFieldValue: (fieldName: keyof Fields, value: any) => void
  startSorting: () => void
  stopSorting: () => void
}

const useComponentController = ({
  sample,
  setSample,
  user,
  sorterUsers,
}: SampleSortingProps): [ControllerState, ControllerActions] => {
  const api = useApi()
  const notifcations = useNotifications()

  const [state, setState, updateState] = useUpdatableState<ControllerState>(initialState)

  const actions = {
    reset: () => {
      const { sorterId, sorterCount, sortTime, sortDate, labSplit, elutriation } = sample

      setState(() => ({
        ...initialState,
        sample,
        userId: user.id,
        values: {
          sorterId: sorterId || '',
          sorterCount,
          sortTime,
          sortDate: sortDate && sortDate?.substring(0, 10),
          labSplitPercent: labSplit && round(labSplit * 100, LAB_SPLIT_PERCENT_PRECISION),
          elutriation: !!elutriation,
        },
      }))
    },
    setFieldValue: (fieldName: keyof Fields, value: unknown) => {
      updateState((draft) => {
        Object.assign(draft.values, { [fieldName]: value })
      })
    },
    startSorting: () => {
      updateState((draft) => {
        draft.editMode = 'started'
        if (!draft.sample?.sorterId) draft.values.sorterId = user.id || ''
        if (!draft.sample?.sortDate) {
          draft.values.sortDate = getToday()
        }
      })
    },
    stopSorting: async () => {
      updateState((draft) => {
        draft.editMode = 'stopping'
      })

      const { sorterId, labSplitPercent, sorterCount, sortTime, sortDate, elutriation } = state.values
      if (
        // TODO: fix this awful guard if possible
        !state.sample?.id ||
        sorterId === '' ||
        labSplitPercent === null ||
        sorterCount === null ||
        sortTime === null ||
        sortDate === null
      )
        throw new Error('Missing values.')
      const updatedSample = {
        sampleId: state.sample.id,
        sorterId,
        labSplit: labSplitPercent / 100,
        sorterCount,
        sortTime,
        sortDate,
        elutriation,
      }
      const success = await updateLabSort(api)(updatedSample)
      if (!success) {
        notifcations.create({ message: 'Failed to update Lab Sort.', type: 'error' })
        updateState((draft) => {
          draft.editMode = 'started'
        })
        return
      }
      setSample({
        ...state.sample,
        ...updatedSample,
        sorterName: sorterUsers.find(({ id }) => id === sorterId)?.fullName ?? state.sample.sorterName,
      })
      updateState((draft) => {
        draft.editMode = 'stopped'
      })
    },
  }

  const errors =
    state.editMode === 'stopped'
      ? {}
      : {
          sortUserId: !state.values.sorterId,
          labSplitPercent: state.values.labSplitPercent === null,
          sorterCount: state.values.sorterCount === null,
          sortTime: state.values.sortTime === null,
        }

  return [{ ...state, errors }, actions]
}

export const SampleSorting = (props: SampleSortingProps): JSX.Element => {
  const { sorterUsers, sample, user } = props

  // STATE

  const [state, actions] = useComponentController(props)
  const {
    values: { sorterId, labSplitPercent, sorterCount, sortTime, sortDate, elutriation },
    errors,
    editMode,
  } = state
  const { setFieldValue } = actions
  const isEditing = editMode === 'started'

  // EFFECTS

  useEffect(() => {
    actions.reset()
  }, [sample])

  // RENDER

  const sorterOptions: SelectFieldProps['options'] = sorterUsers.map((sorterUser) => ({
    value: sorterUser.id,
    label: sorterUser.fullName,
  }))
  if (sample.sorterId && !sorterUsers.find((sorterUser) => sorterUser.id === sorterId))
    sorterOptions.push({
      value: sorterId,
      label: sample.sorterName,
    })

  return (
    <>
      <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
        <SampleInfoPanel
          sample={sample}
          actions={
            <ToggleEditModeButton
              sample={sample}
              isEditing={editMode === 'started'}
              actions={actions}
              hasPermission={user.permissions.updateAny?.includes(NAMCResource.sorting)}
              disabled={Object.values(errors).some((value) => value) || editMode === 'stopping'}
            />
          }
        />
        <div css={{ display: 'grid', gridTemplateColumns: '20em', gap: '1em' }}>
          <SelectField
            label="Sorter"
            value={sorterId}
            onChange={(value) => setFieldValue('sorterId', value)}
            options={sorterOptions}
            error={errors.sorterId}
            readOnly={!isEditing}
          />
          <NumberField
            label="Split %"
            value={labSplitPercent}
            onChange={(value) => setFieldValue('labSplitPercent', value)}
            min={0}
            max={100}
            precision={LAB_SPLIT_PERCENT_PRECISION}
            error={errors.labSplitPercent}
            readOnly={!isEditing}
          />
          <NumberField
            label="Number of bugs"
            value={sorterCount}
            onChange={(value) => setFieldValue('sorterCount', value)}
            min={0}
            max={999999}
            error={errors.sorterCount}
            readOnly={!isEditing}
          />
          <CheckboxField
            label="Elutriation"
            value={elutriation}
            onChange={(value) => setFieldValue('elutriation', value)}
            readOnly={!isEditing}
          />
          <NumberField
            label="Sort time (hours)"
            value={sortTime}
            onChange={(value) => setFieldValue('sortTime', value)}
            min={0}
            max={999999}
            error={errors.sortTime}
            readOnly={!isEditing}
          />
          <DateField
            label="Date sorted"
            value={sortDate}
            onChange={(value) => setFieldValue('sortDate', value)}
            error={errors.sortDate}
            readOnly={!isEditing}
            max={getToday()}
          />
        </div>
      </div>
      {editMode !== 'stopped' && (
        <ConfirmNavigateAway message="Identification is still in progress. Are you sure you want to leave?" />
      )}
    </>
  )
}
