import React from 'react'
import MuiChip from '@mui/material/Chip'
// import CircleIcon from '@mui/icons-material/Circle'
// import SvgIcon from '@mui/material/SvgIcon'

interface ChipProps {
  // variant?: 'standard' | 'reference'
  label: string
  textColor?: string
  chipColor?: string
  onDelete?: () => void
}

// const RoundedSquareIcon = ({ color }: { color: string }) => (
//   <SvgIcon>
//     <rect x="4" y="2" width="20" height="20" rx="2" fill={color} />
//   </SvgIcon>
// )

export const Chip = ({ label, chipColor, textColor, onDelete }: ChipProps): JSX.Element => (
  <MuiChip
    sx={{ backgroundColor: chipColor, color: textColor }}
    label={label}
    // icon={variant === 'standard' ? <CircleIcon sx={{ fill: color }} /> : <RoundedSquareIcon color={color} />}
    variant={chipColor ? 'filled' : 'outlined'}
    onDelete={onDelete}
  />
)
